@import url(https://fonts.googleapis.com/css2?family=Encode+Sans+SC:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
body {
	color: #fff;
	background-color: #4158d0;
	background-image: linear-gradient(
		43deg,
		#4158d0 0%,
		#70ffff 20%,
		#c850c0 46%,
		#4158d0 67%,
		#c850c0 100%
	);
	background-size: 400% 400%;
	font-family: 'Encode Sans SC', sans-serif;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	animation: bg 25s ease-in infinite alternate-reverse;
}
@keyframes bg {
	0%,
	100% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100%, 50%;
	}
}

.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	background: rgba(255, 255, 255, 0.05);
	border-radius: 6px;
	-webkit-backdrop-filter: blur(15px);
	        backdrop-filter: blur(15px);
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	border-left: 1px solid rgba(255, 255, 255, 0.2);
	box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
	padding: 30px;
}
.stopwatch {
	font-size: 40px;
}
button {
	font-family: inherit;
	cursor: pointer;
	color: #fff;
	font-size: 21px;
	padding: 10px 20px;
	background: rgba(255, 255, 255, 0.05);
	border-radius: 6px;
	-webkit-backdrop-filter: blur(15px);
	        backdrop-filter: blur(15px);
	border: none;
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	border-left: 1px solid rgba(255, 255, 255, 0.2);
	box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
	outline: none;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
button:active {
	transform: translate(2px, 2px);
}
.buttons {
	margin-top: 20px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	display: flex;
	grid-gap: 10px;
	gap: 10px;
}

.reset:hover,
.stop:hover {
	background: rgba(255, 0, 0, 0.425);
}
.start:hover {
	background: rgba(59, 209, 13, 0.425);
}
.wait:hover {
	background: rgb(255, 255, 0, 0.425);
}
